import React, { forwardRef } from "react";

import { Icon } from "../Icon/Icon";

import { LinkButtonProps } from "./Button.types";
import { ButtonText, IconWrapper, StyledLinkButton } from "./LinkButton.styles";

const LinkButton = forwardRef<HTMLButtonElement, LinkButtonProps>((props, ref) => {
	const {
		buttonText,
		dataQA,
		leftIcon,
		rightIcon,
	} = props;

	return (
		<StyledLinkButton
			ref={ref}
			data-qa={dataQA}
			{...props}
		>
			{leftIcon
				&& (
					<IconWrapper data-qa={`${dataQA}-left-icon`}>
						<Icon name={leftIcon} size={16} />
					</IconWrapper>
				)}
			<ButtonText
				dataQA="button-text"
				variant="bodyCopyBold"
				component="span"
				color="inherit"
			>
				{buttonText}
			</ButtonText>
			{rightIcon
				&& (
					<IconWrapper data-qa={`${dataQA}-right-icon`}>
						<Icon name={rightIcon} size={16} />
					</IconWrapper>
				)}
		</StyledLinkButton>
	);
});

LinkButton.displayName = "LinkButton";

export default LinkButton;
